import { Card, Form, Grid, InputNumber, Tag, Tooltip, Typography } from '@arco-design/web-react';
import { IconQuestionCircle, IconRight } from '@arco-design/web-react/icon';
import type { ArcoFormItem } from '../util/arco';
import React from 'react';
import type { AmpTrialHtml } from '../data/ampTypes';

const { Row, Col } = Grid;

export const TrialHtml: React.FC<ArcoFormItem<AmpTrialHtml>> = ({ value, onChange }) => {
  return (
    <>
      TrialHtml
      {value}
      <Row>
        <Col span={12}>
          Input
        </Col>
        <Col span={12}>
          Preview
        </Col>
      </Row>
    </>
  )
};


 